<template>
  <div id="event-layout">

    <div id="header-image" v-bind:style="{ backgroundImage: `url(${ bg })` }">
      <h1>{{ title }}</h1>
      <h3>{{ location }}</h3>
      <h5>{{ date }}</h5>
      <!-- <img v-bind:src="require(`../assets/images/${id}/${this.images[randomize(filteredGallery)].thumbnail}`)"> -->
    </div>

    <div id="sidebar">
      <div class="sidebar-content">
        <input id="search-box" type="text" v-model="searchTerm" placeholder="search eg. nissan, toyota, sr20, 200sx" />
        <p id="gallery-results">{{ this.filteredGallery.length }} results found</p>
        <h3>Make</h3>
        <ul>
          <li v-for="make in makes">
            <input type="checkbox" v-bind:id="make" v-bind:value="make" v-model="checkedMakes">
            <label v-bind:for="make">{{ make }}</label><span> ()</span>
          </li>
        </ul>
        <h3>Model</h3>
        <ul>
          <li v-for="model in models">
            <input type="checkbox" v-bind:id="model" v-bind:value="model" v-model="checkedModels">
            <label v-bind:for="model">{{ model }}</label>
          </li>
        </ul>
        <h3>Colour</h3>
        <ul>
          <li v-for="colour in colours">
            <input type="checkbox" v-bind:id="colour" v-bind:value="colour" v-model="checkedColours">
            <label v-bind:for="colour">{{ colour }}</label>
          </li>
        </ul>
      </div>
    </div>


    <div class="gallery-container">
      <h1>{{  }}</h1>
      <div class="gallery-item" v-for="image in filteredGallery">
        <img v-bind:src="require('../assets/images/' + id + '/' + id + '-' + image.thumbnail)">
        <div class="overlay">
          <p>{{ image.make }}</p>
          <p>{{ image.model }}</p>
        </div>
      </div>
    </div>


  </div>
</template>

<script>

import data from '../json/event-data.json'
import sidebar from '../components/sidebar.vue';

export default {
  data() {
    return {
      id: this.$route.params.id,
      title: "",
      location: "",
      date: "",
      searchTerm: '',
      images: [],
      checkedMakes: [],
      checkedModels: [],
      checkedColours: [],
    }
  },
  methods:{
    randomize(theArray) {
      var randint;
      randint = Math.floor(Math.random() * theArray.length + 1);
      return randint;
    },
    uniqueArray: function(arr){
      var unique = [];
      for(var i = (arr.length -1); i>=0; i--){
        var e = arr[i];
        if(unique[0] != e){
          unique.unshift(e);
        }
      }
      return unique;
    },
    makesArrays: function(){
      for(var i = (this.makes.length - 1); i >= 0; i--){
        var e = this.makes[i];
        e = [];
        console.log('---' + this.makes[i] + '---')
        for(let img in this.images){
          if(this.images[img].make === this.makes[i]){
            e.push(this.images[img].model);
          }
        }
        console.log(e.length);
      }
    }
  },
  computed: {
    bg () {
      let logo = 'images/' + this.id + '/' + this.id + '-' + this.randomize(this.images) + '.jpg';

      return require(`../assets/${ logo }`);
    },
    makes: function() {
      var makesArr = [];
      for (var i = (this.images.length - 1); i >= 0; i--) {
        makesArr.push(this.images[i].make);
      }

      return this.uniqueArray(makesArr.sort());
    },
    models: function() {
      var modelsArr = [];
      for (var i = (this.images.length - 1); i >= 0; i--) {
        modelsArr.push(this.images[i].model);
      }

      return this.uniqueArray(modelsArr.sort());
    },
    colours: function() {
      var coloursArr = [];
      for (var i = (this.images.length - 1); i >= 0; i--) {
        coloursArr.push(this.images[i].colour);
      }

      return this.uniqueArray(coloursArr.sort());
    },
    filteredGallery: function() {
      return this.images.filter((image) => {

        var matches = [false, false, false, false];

        if (this.checkedMakes.indexOf(image.make) > -1) {
          matches[0] = true;
        }

        if (this.checkedModels.indexOf(image.model) > -1) {
          matches[1] = true;
        }

        if (this.checkedColours.indexOf(image.colour) > -1) {
          matches[3] = true;
        }

        if (this.checkedMakes.length == 0) {
          matches[0] = true;
        }

        if (this.checkedModels.length == 0) {
          matches[1] = true;
        }

        if (this.checkedColours.length == 0) {
          matches[3] = true;
        }

        if(this.searchTerm != '' && image.tags.indexOf(this.searchTerm) > -1) {
          matches[2] = true;
        }

        if(this.searchTerm != '' && image.make == this.searchTerm) {
          matches[2] = true;
        }

        if(this.searchTerm != '' && image.model == this.searchTerm) {
          matches[2] = true;
        }

        if(this.searchTerm != '' && image.colour == this.searchTerm) {
          matches[2] = true;
        }

        if(this.searchTerm == '') {
          matches[2] = true;
        }

        return matches.indexOf(false) == -1;
      });
    }
  },
  created() {
    this.title = data[this.id].title
    this.location = data[this.id].location
    this.date = data[this.id].date
    this.images = data[this.id].images
  }
}
</script>

<style lang="scss">

#event-layout {
  grid-area: main;
  display: inline-grid;
  grid-template-columns: 1fr 6fr;
  grid-template-rows: 600px auto;
  grid-template-areas:
    "header header"
    "sidebar content"
}

#header-image {
  grid-area: header;
  overflow: hidden;
  background-position: 50% 160%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding: 40px;
  font-family: sans-serif;
  color: white;
  font-size: 4em;
  text-shadow: 4px 4px 7px rgba(0,0,0,0.5);
  h1,h2,h3,h5 {
    margin: 0px;
  }
}

.gallery-container {
  grid-area: content;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;
  padding: 15px;
  box-sizing: border-box;

  .gallery-item {
    position: relative;
    flex-grow: 1;
    max-width: 300px;
    max-height: 200px;
    margin: 2px;
    overflow: hidden;

    .img {
      width: 100%;
    }

    &:hover {
      cursor: pointer;
    }

    &:hover .overlay {
      height: 100%;
      transition: 0.2s ease all;
    }

  }

}

.overlay {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 0px;
  padding: 0px 20px;
  margin-bottom: 5px;
  background: rgba(0,0,0,0.7);
  overflow: hidden;
  text-align: left;
  color: white;
  font-family: sans-serif;
  pointer-events: none;
  transition: 0.2s ease all;

  p {
    margin: 0px;

    &:first-child {
      margin: 20px 0px 5px 0px;
      font-size: 26px;
    }

    &:nth-child(2) {
      font-size: 18px;
    }

  }

}


#sidebar {
  grid-area: sidebar;
  background: white;
  padding: 20px 20px 100px 20px;
  border-right: solid lightgrey 2px;
  text-align: left;
  overflow: hidden;
}

.sidebar-content {
  /*position: fixed;*/
  width: 250px;
  font-family: sans-serif;
}

.sidebar-content ul {
  list-style: none;
  padding: 0px;
  margin: 10px 0px 0px 0px;
}

.sidebar-content h3 {
  margin-top: 30px;
  font-size: 24px;
}

</style>



