<template>
  <div id="app">
    <app-header></app-header>
    <router-view></router-view>
    <app-footer></app-footer>
  </div>
</template>

<script>
import Header from './components/header.vue';
import Footer from './components/footer.vue';
import eventCoverage from './components/eventcoverage.vue';
import posts from './components/posts.vue';

export default {
  components: {
    'app-header': Header,
    'app-footer': Footer
  },
  data () {
    return {
      title: 'Garage Sparks',
    }
  },
  computed: {
    bg () {
      let logo = '0-1-thumb.jpg';

      return require(`./assets/${ logo }`);
    }
  }
}
</script>

<style lang="scss">

html, body {
  padding: 0px;
  margin: 0px;
}
#app {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 70px auto 200px;
  grid-template-areas:
    "header"
    "main"
    "footer";
}

</style>



