<template>
  <div>
    <h1>Posts</h1>
    <p>A list of events</p>
    <div v-for="post in posts">
      <router-link v-bind:to="'/coverage/' + post.id"><h2>{{ post.title }}</h2></router-link>
      <p>{{ post.location }}</p>
      <p>{{ post.date }}</p>
    </div>
  </div>
</template>

<script>

import data from '../json/event-data.json'

export default {
  data() {
    return {
      posts: []
    }
  },
  created() {
    this.posts = data
  }
}
</script>

<style lang="scss">

</style>



